<template>
    <div>
        <u-container width="500px" height="100px">
            <img slot="background" :src="gridImg" />
            <img slot="foreground" :src="btnImg" />
        </u-container>
        <u-container width="160px" height="80px">
            <u-button slot="background" width="160px" height="80px" :skin="img12" rule="1,3||20,10,20,10"></u-button>
            <img slot="foreground" :src="btnImg" />
        </u-container>
        <u-container width="160px" height="80px">
            <u-button slot="background" width="160px" height="80px" :skin="img12" rule="1,3||20,10,20,10"></u-button>
            <u-button slot="foreground" width="104px" height="24px" :skin="imgGroup" rule="||23,0,27,0"></u-button>
        </u-container>
        <br />
        <u-container width="100%" height="100px">
            <u-button slot="background" width="100%" height="80px" :skin="img12" rule="1,3||20,10,20,10"></u-button>
            <u-button slot="foreground" width="104px" height="24px" :skin="imgGroup" rule="||23,0,27,0"></u-button>
        </u-container>
    </div>
</template>
<script>
    import img from './img/barragefullscreen_btn.png';
    import imgtoa from './img/img_tip_bg.png';
    import img12 from './img/carddating/btn_on.png';
    import group1 from './img/button/but_edit_normal.png';
    import group2 from './img/button/but_edit_hover.png';
    import group3 from './img/button/but_edit_press.png';

    export default {
        data () {
            return {
                btnImg: null,
                img12,
                gridImg: imgtoa,
                index: 1,
                imgGroup: [group1, group2, group3],
            };
        },
        mounted () {
            // this.index = (++this.index) % 2;
            var texture = this.$Core.App.assets().getTexture(img);
            texture && texture.on(this.$Core.Texture.ONUPDATED, (texture) => {
                this.onClipsLoaded(texture);
            });
            this.onClipsLoaded(texture);

            texture = this.$Core.App.assets().getTexture(imgtoa);
            texture && texture.on(this.$Core.Texture.ONUPDATED, (texture) => {
                this.onGridLoaded(texture);
            });
            this.onGridLoaded(texture);
        },
        methods: {
            onClipsLoaded(texture) {
                if (!texture || !texture.valid) {
                    return;
                }
                const clips = (texture && texture.toClips(3, 1, 1)) || [];
                texture = (clips.length > this.index) && clips[this.index] || null;
                texture && texture.on(this.$Core.Texture.ONUPDATED, () => {
                    this.btnImg = (texture && texture.dataURL) || '';
                });
                this.btnImg = (texture && texture.dataURL) || '';
            },
            onGridLoaded(texture) {
                if (!texture || !texture.valid) {
                    return;
                }
                const gridTexture = (texture && texture.toGrid(10, 10, 78, 15, 500, 100));
                gridTexture && gridTexture.on(this.$Core.Texture.ONUPDATED, () => {
                    this.gridImg = (gridTexture && gridTexture.dataURL) || '';
                });
                this.gridImg = (gridTexture && gridTexture.dataURL) || '';
            }
        }
    };
</script>
<style>
</style>



